.addUserForm {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.users {


  &__list {
    display: flex;
    flex-wrap: wrap;
    border: 1px solid black;
    padding: 10px;
    gap: 15px;
    list-style-type: none;

    & li {
      margin: 0 auto;
      border: 1px solid black;
      padding: 10px;
      width: 300px;

      & a {
        display: block;
        margin-bottom: 5px;
      }
    }
  }
}